"use client"

import { useState } from "react"
import { useParams } from "next/navigation"
import Link from "next/link"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { mockJobs, mockCandidates } from "@/lib/mock-data"
import { MapPin, Briefcase, Calendar, CheckCircle2 } from "lucide-react"

export default function JobDetailPage() {
  const params = useParams()
  const job = mockJobs.find((j) => j.id === params.id)
  const [hasApplied, setHasApplied] = useState(false)
  const [selectedCV, setSelectedCV] = useState<string | null>(null)

  if (!job) {
    return (
      <div className="p-6">
        <p className="text-muted-foreground">Job not found</p>
      </div>
    )
  }

  const handleApply = () => {
    if (selectedCV) {
      setHasApplied(true)
      setTimeout(() => {
        setHasApplied(false)
        setSelectedCV(null)
      }, 3000)
    }
  }

  return (
    <div className="p-6 space-y-8">
      {/* Header */}
      <div>
        <Link href="/jobs" className="text-primary hover:underline text-sm mb-4 block">
          ‚Üê Back to jobs
        </Link>
        <h1 className="text-4xl font-bold text-foreground mb-2">{job.title}</h1>
        <p className="text-xl text-muted-foreground">{job.company}</p>
      </div>

      <div className="grid md:grid-cols-3 gap-8">
        {/* Main Content */}
        <div className="md:col-span-2 space-y-6">
          {/* Job Details */}
          <Card>
            <CardHeader>
              <CardTitle>Job Details</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid md:grid-cols-2 gap-4">
                <div className="flex items-center gap-3">
                  <MapPin className="h-5 w-5 text-primary" />
                  <div>
                    <p className="text-sm text-muted-foreground">Location</p>
                    <p className="font-semibold text-foreground">{job.location}</p>
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <Briefcase className="h-5 w-5 text-primary" />
                  <div>
                    <p className="text-sm text-muted-foreground">Employment Type</p>
                    <p className="font-semibold text-foreground">{job.type}</p>
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <span className="text-2xl font-bold text-primary">${job.salary.split("-")[0]}</span>
                  <div>
                    <p className="text-sm text-muted-foreground">Salary Range</p>
                    <p className="font-semibold text-foreground">{job.salary}</p>
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <Calendar className="h-5 w-5 text-primary" />
                  <div>
                    <p className="text-sm text-muted-foreground">Posted</p>
                    <p className="font-semibold text-foreground">{job.postedDate}</p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Description */}
          <Card>
            <CardHeader>
              <CardTitle>About the Role</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-foreground mb-4">{job.description}</p>
              <p className="text-sm text-muted-foreground">
                Join our team and make an impact. We're looking for passionate individuals ready to take on challenges
                and grow with us.
              </p>
            </CardContent>
          </Card>

          {/* Requirements */}
          <Card>
            <CardHeader>
              <CardTitle>Requirements</CardTitle>
            </CardHeader>
            <CardContent>
              <ul className="space-y-3">
                {job.requirements.map((req, idx) => (
                  <li key={idx} className="flex items-start gap-3">
                    <CheckCircle2 className="h-5 w-5 text-primary mt-0.5 flex-shrink-0" />
                    <span className="text-foreground">{req}</span>
                  </li>
                ))}
              </ul>
            </CardContent>
          </Card>
        </div>

        {/* Sidebar */}
        <div className="space-y-4 h-fit sticky top-24">
          {/* Skills Match */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Required Skills</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex flex-wrap gap-2">
                {job.skills.map((skill) => (
                  <Badge key={skill} variant="secondary">
                    {skill}
                  </Badge>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Apply Section */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Apply Now</CardTitle>
              <CardDescription>Select your CV to apply</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              {hasApplied ? (
                <div className="flex flex-col items-center justify-center py-6">
                  <CheckCircle2 className="h-12 w-12 text-green-600 mb-2" />
                  <p className="font-semibold text-foreground">Application Sent!</p>
                  <p className="text-sm text-muted-foreground">Good luck with your application</p>
                </div>
              ) : (
                <>
                  <div className="space-y-2 max-h-32 overflow-y-auto">
                    {mockCandidates.map((candidate) => (
                      <div
                        key={candidate.id}
                        onClick={() => setSelectedCV(candidate.id)}
                        className={`p-3 border rounded-lg cursor-pointer transition-colors ${
                          selectedCV === candidate.id
                            ? "bg-primary/10 border-primary"
                            : "hover:bg-secondary border-border"
                        }`}
                      >
                        <p className="font-medium text-foreground">{candidate.name}</p>
                        <p className="text-xs text-muted-foreground">{candidate.title}</p>
                      </div>
                    ))}
                  </div>
                  <Button className="w-full" disabled={!selectedCV} onClick={handleApply}>
                    Apply with CV
                  </Button>
                </>
              )}
            </CardContent>
          </Card>

          {/* Company Info */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Company</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                <div>
                  <p className="text-sm text-muted-foreground">Company Name</p>
                  <p className="font-semibold text-foreground">{job.company}</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Total Applications</p>
                  <p className="font-semibold text-foreground">{job.applicationCount}</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
